.splash {
  background-image: none;
  padding-top: 125px;
  @media screen and (max-width: $tablet-width) {
    background-image: url(../images/how-it-made/mobile/m-splash-of-milk-min.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }
  @media (min-width: $desktop-width) {
    background-image: url(../images/how-it-made/mobile/m-splash-of-milk-min.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;

    background-color: #fff5f6;
  }
}

.how-second-titel {
  font-family: $title-fonte-family;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: $secondary-title-color;
  margin-bottom: 15px;
  @media screen and (min-width: $tablet-width) {
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 16px;
  }
  @media screen and (min-width: $desktop-width) {
    font-size: 30px;
    line-height: 34px;
    text-align: center;
    letter-spacing: 0.04em;
    margin-bottom: 15px;
  }
}
.how-fitst-title {
  width: 200px;
  font-family: $title-fonte-family;
  font-size: 34px;
  line-height: 39px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $primary-title-color;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 26px;
  @media screen and (min-width: $tablet-width) {
    font-size: 48px;
    line-height: 55px;
    letter-spacing: 0.04em;
    width: 440px;
  }
  @media screen and (min-width: $desktop-width) {
    font-size: 58px;
    line-height: 66px;
    letter-spacing: 0.06em;
    width: 634px;
  }
}
.how-dark-text {
  width: 220px;
  font-family: $main-fonte-family;
  font-weight: 500;
  font-size: 12px;
  line-height: 19px;
  color: $secondary-text-color;
  margin-bottom: 47px;
  @media screen and (min-width: $tablet-width) {
    width: 340px;
    font-size: 16px;
    line-height: 176%;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: $desktop-width) {
    width: 463px;
  }
}
.how-light-text {
  width: 220px;
  font-family: $main-fonte-family;
  font-weight: 500;
  font-size: 12px;
  line-height: 162%;
  color: $third-text-color;
  margin-bottom: 17px;
  @media screen and (min-width: $tablet-width) {
    width: 340px;
    font-size: 14px;
    line-height: 194%;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: $desktop-width) {
    width: 463px;
  }
}

.btn-read-more {
  font-family: $main-fonte-family;
  font-style: normal;
  font-weight: bold;
  display: inline;
  width: 176px;
  height: 44px;
  font-size: 14px;
  line-height: 18px;
  align-items: center;
  text-align: center;
  border-radius: 22px;
  border-style: none;
  background-color: $main-text-color;
  color: $accent-color;
  margin-bottom: 39px;
  cursor: pointer;
  @media screen and (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 21px;
  }
}
.how-size {
  width: 280px;
  height: 279px;
  margin-bottom: 46px;
  margin-left: auto;
  margin-right: auto;
  object-fit: cover;

  @media screen and (min-width: 768px) {
    width: 340px;
    height: 339px;
    margin-right: 0;
    margin-left: 0;
  }

  @media screen and (min-width: 1090px) {
    width: 516px;
    height: 516px;
    margin-right: 74px;
    object-fit: cover;
  }
}

.how-splash {
  @media screen and (min-width: $tablet-width) {
    width: 740px;
    display: flex;
  }
  @media screen and (min-width: $desktop-width) {
    width: 1090px;
    display: flex;
  }
}
.how-text-position {
  padding: 0 30px;
  @media screen and (min-width: $desktop-width) {
    width: 1090px;
    padding-top: 47px;
  }
}
.how-desktop {
  @media screen and (min-width: $tablet-width) {
    display: flex;
  }
}

.measure {
  font-family: $title-fonte-family;
  color: $primary-title-color;
  font-size: 36px;
  line-height: 41px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 16px;
  @media screen and (min-width: $tablet-width) {
    font-size: 58px;
    line-height: 66px;
    letter-spacing: 0.04em;
    margin-bottom: 13px;
  }
  @media screen and (min-width: $desktop-width) {
    font-size: 58px;
    line-height: 66px;
    letter-spacing: 0.04em;
    margin-bottom: 15px;
  }
}

.how-text2 {
  font-family: $main-fonte-family;
  color: $secondary-text-color;
  font-weight: 500;
  font-size: 12px;
  line-height: 162%;
  width: 230px;
  height: 36px;
  margin-bottom: 38px;
  @media screen and (min-width: $tablet-width) {
    font-size: 14px;
    line-height: 174%;
    width: 180px;
    height: 67px;
  }
  @media screen and (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 176%;
    letter-spacing: 0.02em;
    width: 330px;
    height: 52px;
  }
}
.span {
  display: block;
  margin-top: 10px;
}
.li-tablet {
  @media screen and (min-width: $tablet-width) {
    margin-right: 100px;
  }
  @media screen and (min-width: $desktop-width) {
    margin-right: 30px;
  }
}
.li-tablet:last-child {
  @media screen and (min-width: $tablet-width) {
    margin-right: 0;
  }
}

.cow-img {
  width: 280px;
  height: 279px;

  @media (min-width: $tablet-width) {
    width: 340px;
    height: 339px;
  }

  @media (min-width: $desktop-width) {
    width: 540px;
    height: 540px;
  }
}
